@import url("https://fonts.googleapis.com/css2?family=Anton&family=Oswald:wght@400;600&family=Roboto:wght@400;500&display=swap");
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.app {
  max-width: 1980px;
  margin: 0 auto;
}
.content {
  display: flex;
  justify-content: center;
  padding: 40px;
}

.detail {
  flex: 1 1 60%;
}
.list {
  flex: 1 1 40%;
}

@media screen and (max-width: 48rem) {
  .content {
    flex-direction: column;
    padding: 0;
    margin: 20px;
  }
}
